var infoCode;
export default {
	data() {
		return {
			title: 'Hello'
		}
	},
	onLoad() {

		uni.login({
			provider:'weixin',
			success:function(res){
				console.log(res);
				infoCode = res.code;
			}
		})
		console.log(this.globel_url);
		this.user_info.nickName = 'breamer';

		

	},
	methods: {
		getUserInfo: function(res) {
			if (!res.detail.iv) {
				uni.showToast({
					title: "您取消了授权,登录失败",
					icon: "none"
				});
				return false;
			}
			console.log(res.detail);
			//
			uni.request({
				url: this.globel_url + 'user/wx/info',
				method: 'GET',
				data: {
					jsCode: infoCode
				},
				dataType: 'json',
				success: (res) => {
					console.log(res);
				}
			});
		}
	}
}
